import { Link } from "gatsby";
import React, { FC } from "react";
import styled from "styled-components";
import { device } from "../../../../utils/layoutBreakpoints";
import SeparatorLine from "../SeparatorLine/SeparatorLine";
import DesktopNavSection from "./DesktopNavSection/DesktopNavSection";
import { footerNavigationItems } from "./FooterNavigationItems/footerNavigationItems";
import MobileNavSection from "./MobileNavSection/MobileNavSection";

const StyledFooterNavigation = styled.nav``;

const StyledDesktopNavigation = styled.div`
  display: none;
  flex-direction: column;
  gap: 60px;
  & > div:first-child {
    display: flex;
    justify-content: space-between;
    gap: 50px;
  }
  & > div:nth-child(2) {
    display: flex;
    justify-content: flex-end;
    gap: 20px;
    font-size: 0.6rem;
    div {
      position: relative;
    }
    & > div:not(:last-child)::after {
      display: block;
      content: "|";
      position: absolute;
      right: -10px;
      top: 0px;
    }
  }
  @media ${device.laptop} {
      display: flex;
    }
`;

const StyledMobileNavigation = styled.div`
  display: block;
  @media ${device.laptop} {
    display: none;
  }
`;

const FooterNavigation: FC = () => {
  const legalSection = footerNavigationItems[footerNavigationItems.length - 1];

  return (
    <StyledFooterNavigation>
      <StyledDesktopNavigation>
        <div>
          {footerNavigationItems.slice(0, -1).map((item) => (
            <DesktopNavSection navigationItem={item} key={item.sectionTitle}/>
          ))}
        </div>
        <div>
          {legalSection.links.map((link) => {
            return (
              <div key={link.linkId}>
                <Link to={link.url}>{link.title}</Link>
              </div>
            );
          })}
        </div>
      </StyledDesktopNavigation>
      <StyledMobileNavigation>
        <SeparatorLine margin="0 0 10px 0"/>
        {footerNavigationItems.map((item) => (
          <MobileNavSection navigationItem={item} key={item.sectionTitle} />
        ))}
      </StyledMobileNavigation>
    </StyledFooterNavigation>
  );
};

export default FooterNavigation;
