import { Link } from "gatsby";
import React, { FC, useContext, useState } from "react";
import styled from "styled-components";
import { colors } from "../../../utils/colors";
import { device } from "../../../utils/layoutBreakpoints";
import { Container } from "../Container/Container";
import BurgerMenuIcon from "../Icons/BurgerMenuIcon/BurgerMenuIcon";
import DesktopLogo from "../Logo/DesktopLogo";
import MobileLogo from "../Logo/MobileLogo";
import MainNavigation from "./MainNavigation/MainNavigation";
import ServiceNavigation from "./DesktopNavigation/ServiceNavigation/ServiceNavigation";
import MobileNavigation from "./MobileNavigation/MobileNavigation";
import { ExternalLinks, InternalLinks } from "../../../utils/links";
import {
  headerNavigationItems,
  HeaderNavigationSections,
} from "./HeaderNavigation/HeaderNavigationItem/headerNavigationItems";
import PhoneIcon from "../Icons/PhoneIcon/PhoneIcon";
import PersonIcon from "../Icons/PersonIcon/PersonIcon";
import { AppContext } from "../Layout/Layout";
import HeaderPromoBanner from "../HeaderPromoBanner";

const StyledHeader = styled.header`
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  height: 64px;
  padding-top: 15px;
  box-sizing: border-box;
  color: ${colors.primary.black};
  background-color: ${colors.primary.white};
  @media ${device.laptop} {
    height: 120px;
    padding-top: 0;
  }
  position: sticky;
  z-index: 10;
  top: 0;
  @media ${device.laptop} {
    top: -40px;
  }
`;

const DesktopHeaderWrapper = styled.div`
  justify-content: space-between;
  display: none;
  padding-top: 25px;
  @media ${device.laptop} {
    display: flex;
  }
`;

const MobileHeaderWrapper = styled.div`
  font-family: "Rubik";
  font-size: 0.8rem;
  font-weight: 500;
  & > div:first-child {
    justify-content: space-between;
    display: flex;
    align-items: center;
    & > div {
      width: 100%;
    }
    & > div:nth-child(2) {
      text-align: center;
    }
    & > div:nth-child(3) {
      text-align: right;
    }
  }
  @media ${device.laptop} {
    display: none;
  }
`;

const Wrapper = styled.div`
  display: flex;
  gap: 50px;
  align-items: center;
`;

const LoginSectionContainer = styled.div`
  background: ${colors.secondary.gray100};
  display: none;

  @media ${device.laptop} {
    display: block;
  }
`;

const LoginSection = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 5px 0;
  font-family: "Rubik";
  font-weight: 400;
  font-size: 0.8rem;
  gap: 15px;
  a {
    display: flex;
    align-items: center;
    gap: 5px;
  }
`;

const Header: FC = () => {
  const [isMobileNavigationOpened, setIsMobileNavigationOpened] =
    useState<boolean>(false);

  const loginLink = headerNavigationItems
    .find(
      (section) =>
        section.sectionTitle === HeaderNavigationSections.ServiceNavigation
    )
    ?.links.find((link) => link.linkId === "header_navigation_link_login");

  const appContext = useContext(AppContext);

  return (
    <StyledHeader>
      <LoginSectionContainer>
        <Container>
          <LoginSection>
            <a href="tel:(888) 835-4666">
              <PhoneIcon />
              (888) 835-4666
            </a>
            <a href={loginLink?.url} target={"__blank"}>
              <PersonIcon />
              {loginLink?.title}
            </a>
          </LoginSection>
        </Container>
      </LoginSectionContainer>
      <Container>
        <DesktopHeaderWrapper>
          <Wrapper>
            <Link to={InternalLinks.HomePage}>
              <DesktopLogo />
            </Link>
            <MainNavigation />
          </Wrapper>
          <Wrapper>
            <ServiceNavigation />
          </Wrapper>
        </DesktopHeaderWrapper>
        <MobileHeaderWrapper>
          <div>
            <div onClick={() => setIsMobileNavigationOpened((st) => !st)}>
              <BurgerMenuIcon isOpen={isMobileNavigationOpened} />
            </div>
            <div>
              <Link to={InternalLinks.HomePage}>
                <MobileLogo />
              </Link>
            </div>
            <div>
              <Link
                to={"/"}
                onClick={(e) => {
                  e.preventDefault();
                  appContext.getADemoModal.setShowGetADemoModal(true);
                }}
              >
                Get a Demo
              </Link>
            </div>
          </div>
        </MobileHeaderWrapper>
      </Container>
      <MobileNavigation
        isOpen={isMobileNavigationOpened}
        setIsMobileNavigationOpened={setIsMobileNavigationOpened}
      />
    </StyledHeader>
  );
};

export default Header;
