import { Link } from "gatsby";
import React, { FC } from "react";
import { colors } from "../../../utils/colors";
import { InternalLinks } from "../../../utils/links";
import { Container } from "../Container/Container";
import CrossIcon from "../Icons/CrossIcon/CrossIcon";
import { CloseButton, StyledHeaderPromoBanner } from "./styled";

type Props = {
  onClick: () => void;
};

const HeaderPromoBanner: FC<Props> = ({ onClick }) => {
  return (
    <StyledHeaderPromoBanner >
      <Container>
        <p>
          Hey Zipwhip customers, we can help you migrate to Telgorithm's
          messaging APIs instantly!{" "}
          <Link to={InternalLinks.ContactUs}>
            Contact us to learn more about our streamlined process.
          </Link>
        </p>
        <CloseButton onClick={onClick}>
          <CrossIcon color={colors.primary.white}></CrossIcon>
        </CloseButton>
      </Container>
    </StyledHeaderPromoBanner>
  );
};

export default HeaderPromoBanner;
