import { Link } from "gatsby";
import React, { FC } from "react";
import styled from "styled-components";
import { NavigationItemLink } from "../../../../types/NavigationItem";
import { colors } from "../../../../utils/colors";
import { device } from "../../../../utils/layoutBreakpoints";
import {
  headerNavigationItems,
  HeaderNavigationSections,
} from "../HeaderNavigation/HeaderNavigationItem/headerNavigationItems";

const StyledMainNavigation = styled.nav`
  display: flex;
  gap: 20px;
  font-family: "Rubik";
  font-weight: 500;
  font-size: 0.9rem;

  @media ${device.desktop} {
    gap: 35px;
  }
`;

const SubNavigationDropdown = styled.div`
  background-color: ${colors.primary.white};
  width: auto;
  height: auto;
  padding: 30px;
  position: absolute;
  top: 70px;
  left: -9999px;
  display: flex;
  flex-direction: column;
  gap: 25px;
  white-space: nowrap;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  box-sizing: border-box;
  opacity: 0;
  transition: opacity 0.5s, left 0s 0.5s;
  &:hover {
    opacity: 1;
    left: -50%;
    transition: opacity 0.5s, left 0s 0s;
  }
  &::before {
    content: "";
    top: -50px;
    position: absolute;
    width: 100%;
    height: 65px;
    left: 0;
  }
  &::after {
    content: "";
    width: 100%;
    height: 30px;
    position: absolute;
    border-radius: 5px;
    top: 0;
    left: 0;
    background-color: ${colors.primary.white};
  }
`;

const SubNavigationContainer = styled.div`
  position: relative;
  height: 100%;
  span {
    cursor: pointer;
  }

  &:hover {
    ${SubNavigationDropdown} {
      opacity: 1;
      left: -50%;
      transition: opacity 0.5s, left 0s 0s;
    }
  }
`;

const Arrow = styled.div`
  display: block;
  position: absolute;
  width: 25px;
  height: 25px;
  top: -12px;
  right: 55%;
  background-color: ${colors.primary.white};
  transform: rotate(45deg);
  box-sizing: border-box;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
`;

const MainNavigation: FC = () => {
  const renderLinks = (links: NavigationItemLink[]) => {
    return links.map((link) =>
      link.external ? (
        <a href={link.url} target="_blank" key={link.linkId} rel="noreferrer">
          {link.title}
        </a>
      ) : link.subLinks?.length ? (
        <SubNavigationContainer key={link.linkId}>
          <span>{link.title}</span>
          <SubNavigationDropdown>
            <Arrow></Arrow>
            {renderLinks(link.subLinks)}
          </SubNavigationDropdown>
        </SubNavigationContainer>
      ) : (
        <Link to={link.url} key={link.linkId}>
          {link.title}
        </Link>
      )
    );
  };

  return (
    <StyledMainNavigation>
      {renderLinks(
        headerNavigationItems.find(
          (section) =>
            section.sectionTitle === HeaderNavigationSections.MainNavigation
        )?.links || []
      )}
    </StyledMainNavigation>
  );
};

export default MainNavigation;
