import styled from "styled-components";
import { colors } from "../../../utils/colors";
import { device } from "../../../utils/layoutBreakpoints";

export const StyledHeaderPromoBanner = styled.div`
  position: static;
  z-index: 11;
  top: 0px;
  padding: 10px;
  background-color: ${colors.secondary.orange};
  width: 100%;
  font-weight: 400;
  color: ${colors.primary.white};
  box-sizing: border-box;
  text-align: center;

  font-size: 0.6rem;
  line-height: 0.8rem;

  div {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
  }

  a {
    border-bottom: 1px solid ${colors.primary.white};
  }

  a:hover {
    color: ${colors.primary.white};
    border-bottom: none;
  }

  @media ${device.tablet} {
    font-size: 0.8rem;
    line-height: 1.2rem;
    font-weight: 500;
  }

  @media ${device.desktop} {
    p {
      width: 100%;
    }
  }
`;

export const CloseButton = styled.div`
  cursor: pointer;
  transition: 0.2s;
  &:hover {
    transition: 0.2s;
    scale: 1.2;
  }
`;
