import React, { FC, useEffect, useState } from "react";
import styled from "styled-components";
import { colors, opacityToHex } from "../../../../utils/colors";
import { device } from "../../../../utils/layoutBreakpoints";
import { PrimaryButton, SecondaryButton } from "../../Buttons/Buttons";
import SuccessFormSubmitMessage from "../../SuccessFormSubmitMessage/SuccessFormSubmitMessage";
import Cookies from "js-cookie";

const StyledSubscribe = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  color: ${colors.primary.white};
  font-weight: 700;
`;

const StyledInput = styled.input`
  background-color: ${colors.primary.white + opacityToHex["0.1"]};
  border-radius: 6px;
  height: 32px;
  border: none;
  outline: none;
  padding: 10px;
  box-sizing: border-box;
  color: ${colors.primary.white};
  font-family: "Rubik";
  width: 100%;
  @media ${device.tablet} {
    width: 260px;
  }
`;

const Wrapper = styled.div`
  display: flex;
  gap: 10px;
  align-items: center;
  width: 100%;
  div:first-child {
    width: 100%;
    @media ${device.tablet} {
      width: auto;
    }
  }
`;

const Subscribe: FC = () => {
  const [isSubmitted, setIsSubmitted] = useState<boolean>(false);
  const [error, setError] = useState<boolean>(false);

  const [email, setEmail] = useState<string>("");

  useEffect(() => {
    if (error) setTimeout(() => setError(false), 3000);
  }, [error]);

  const onSubmit = () => {
    const data = {
      fields: [
        {
          objectTypeId: "0-1",
          name: "email",
          value: email,
        },
      ],
      context: {
        hutk: Cookies.get("hubspotutk"),
        pageUri: document.location.href,
        pageName: document.title,
      },
    };

    fetch(
      "https://api.hsforms.com/submissions/v3/integration/submit/21630623/59a24190-cebd-4899-a4ed-9e677896b741",
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(data),
      }
    )
      .then(() => setIsSubmitted(true))
      .catch(() => setError(true));
  };

  return (
    <StyledSubscribe>
      {!isSubmitted && <div>Subscribe to our newsletter</div>}
      {!isSubmitted ? (
        <form
          onSubmit={(e) => {
            e.preventDefault();
            onSubmit();
          }}
          id="59a24190-cebd-4899-a4ed-9e677896b741"
        >
          <Wrapper>
            <div>
              <StyledInput
                placeholder="Work email"
                name="email"
                type="email"
                value={email}
                onChange={(e) => setEmail(e.currentTarget.value)}
              ></StyledInput>
            </div>
            <div>
              <SecondaryButton
                height="32px"
                padding="0 15px"
                type="submit"
                form="59a24190-cebd-4899-a4ed-9e677896b741"
              >
                Subscribe
              </SecondaryButton>
            </div>
          </Wrapper>
        </form>
      ) : error ? <div>Something Went Wrong, Please Try Again</div> : (
        <SuccessFormSubmitMessage />
      )}
    </StyledSubscribe>
  );
};

export default Subscribe;
