import { Link } from "gatsby";
import React, { FC } from "react";
import styled from "styled-components";
import { NavigationItem } from "../../../../../types/NavigationItem";

interface Props {
  navigationItem: NavigationItem;
}

const StyledDesktopNavSection = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  & > div:first-child {
    font-size: 0.7rem;
    font-weight: 700;
  }
  & > div:nth-child(2) {
    font-size: 0.6rem;
    display: flex;
    flex-direction: column;
    gap: 5px;
    justify-content: flex-start;
  }
`;

const DesktopNavSection: FC<Props> = ({ navigationItem }) => {
  return (
    <StyledDesktopNavSection>
      <div>{navigationItem.sectionTitle}</div>
      <div>
        {navigationItem.links.map((link) => {
          return (
            <div key={link.linkId}>
              {link.external ? (
                <a href={link.url} target="_blank" key={link.linkId} rel="noreferrer">
                  {link.title}
                </a>
              ) : (
                <Link to={link.url} key={link.linkId}>
                  {link.title}
                </Link>
              )}
            </div>
          );
        })}
      </div>
    </StyledDesktopNavSection>
  );
};

export default DesktopNavSection;
